<template>
  <div class="goods-main">
       <router-view></router-view>
  </div>


</template>

<script>
export default {
  methods:{
    getAuth(){
        this.axios.post(this.myConfig.code.auth + '/api/authority', {}).then(response => {
            let data = response.data;
            if (data.head.code == "10000") {//权限获取成功
                //存储权限列表
                this.$store.dispatch('setAuthority', data.body);
                this.$store.dispatch('setAuthSelect', data.body.menus[0]);  //一级菜单列表
                localStorage.setItem('headMenu', data.body.menus[0].menuId);
                localStorage.setItem('setAuthSelect', JSON.stringify(data.body.menus[0]));
                //存储操作列表
                let opsArr = [];
                let opsName = [];
                let getAllOps = function (arr) {
                    for (let i in arr) {
                        opsArr = opsArr.concat(arr[i].ops);
                        opsName.push(arr[i].menuUri)
                        if (arr[i].childMenu) {
                            getAllOps(arr[i].childMenu)
                        }
                    }
                }
                getAllOps(data.body.menus);
                for (let j in opsArr) {
                    opsName.push(opsArr[j].opUri)
                }
                opsName = Array.from(new Set(opsName))
                this.$store.dispatch('setAuthOps', opsName);

            }
        })      
    }
  },
  created(){
    this.getAuth();
  },
  watch:{
    "$route":function(){
         this.getAuth();
    }
  }
}
</script>



